import { ApprovalModule } from 'src/app/components/approval/approval.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TicketRoutingModule } from './ticket-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { DetalComponent } from './detal/detal.component';
import { SearhItemComponent } from './searh-item/searh-item.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PipeModule } from 'src/app/pipe/pipe.module';


@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent, DetalComponent, SearhItemComponent],
    imports: [
        CommonModule,
        TicketRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        PerfectScrollbarModule,
        ApprovalModule,
        PipeModule
    ],
    providers:
        [
            FormBuilderTypeSafe
        ],
    entryComponents: [
        SearhItemComponent
    ]
})
export class TicketModule { }
